/** Blocks terminaux contenant des balises inline (txt-para, txt-member, txt-caption). */
export abstract class TxtParaLike extends TxtBlock {

	get isTextContainer(): boolean {return true}

	get strModel(): TxtStrModel {return this.model.strModel || this.txtRoot.wedConfig.strModel}

	//refreshBindValue(val: IJmlNode | string) {
	//super.refreshBindValue(val);
	//if (!this.tailNode) this.tailNode = this.appendChild(document.createElement("br"));
	//}

	get txtWedModels(): TxtModel[] {return this.model.txtWedModels || this.txtRoot.wedConfig.preferedInlineTxtModels}

	onChildWedletsChange() {
		adjustVirtualsStr(this, this.tailNode);
	}

	/**
	 * Retourne false si interdit par le modle. Retourne true si rellement insr
	 * ou fusionn avec un autre para vide.
	 */
	addNextPara(batch: IDocBatch, content?: IJmlSet): boolean {
		const parent = findTxtEltOrRootParent(this);
		if (parent instanceof TxtRoot) {
			if (this.wedMgr.docHolder.getInsertableOffset(parent.wedlet.wedAnchor, ENodeType.element, 'start', parent.paraModel.nodeName) < 0) return false;
		} else if (!(parent instanceof TxtSL || IS_TxtParaParent(parent))) return false;
		const model = parent.paraModel;
		if (!content || content.length === 0) {
			if (this.isEmpty()) return true;
			const next = findTxtEltNextSibling(this);
			if (next && next.model === model && next.isEmpty()) return true;
		}
		const jml = content ? [model.newJml(), content] : [model.newJml()];
		batch.insertJml(XA.incrAtDepth(this.wedAnchor, -1, 1), jml);
		return true;
	}

	/**
	 * Retourne false si interdit par le modle. Retourne true si rellement insr
	 * ou fusionn avec un autre para vide.
	 */
	addPreviousPara(batch: IDocBatch, content?: IJmlSet): boolean {
		const parent = findTxtEltOrRootParent(this);
		if (parent instanceof TxtRoot) {
			if (this.wedMgr.docHolder.getInsertableOffset(parent.wedlet.wedAnchor, ENodeType.element, 'start', parent.paraModel.nodeName) < 0) return false;
		} else if (!(parent instanceof TxtSL || IS_TxtParaParent(parent))) return false;
		const model = parent.paraModel;
		if (!content || content.length === 0) {
			if (this.isEmpty()) return true;
			const prev = findTxtEltPreviousSibling(this);
			if (prev && prev.model === model && prev.isEmpty()) return true;
		}
		const jml = content ? [model.newJml(), content] : [model.newJml()];
		batch.insertJml(this.wedAnchor, jml);
		return true;
	}

	isMergeable(next: TxtElement): boolean {return next && next instanceof TxtParaLike && !(next instanceof TxtTitle)}

	moveAsStandardPara(ctnTarget: TxtElement | TxtRoot, offsetTarget: number, batch: IDocBatch) {
		if (this.isVirtual()) return;
		//On cre une nouvelle enveloppe de para standard
		const para = [ctnTarget.paraModel.newJml(), this.exportContent()] as IJmlSet;
		//On supprime ce para
		batch.deleteSequence(this.wedAnchor, 1);
		//On insre le nouveau
		batch.insertJml(XA.append(ctnTarget.wedAnchor, offsetTarget), para);
	}

	deleteByCaret(fromInside: boolean, backward: boolean, batch: IDocBatch) {
		function findMergeableParaLikeOrBlock(n: Node): n is TxtElement {
			if (n instanceof TxtParaLike) return true; // ParaLike
			if (IS_TxtBlock(n)) return n.isUnwrappable() !== "list"; // bloc non fusionnable (object, table, div...)
			return false;
		}

		if (backward) {
			if (!(this instanceof TxtTitle) || !this.mustKeepAsVirtual()) {
				//Ce paraLike pourrait tre supprim
				//On cherche un paraLike prcdent avec lequel merger
				const prev = DOM.findPrevious(this, this.txtRoot, findMergeableParaLikeOrBlock);
				if (prev && prev instanceof TxtParaLike) {
					prev.merge(this, batch);
					return;
				}
			}
		} else {
			//On cherche un paraLike suivant avec lequel merger
			const next = DOM.findNextUncle(this, this.txtRoot, findMergeableParaLikeOrBlock);
			if (next && next instanceof TxtParaLike && !next.isVirtual() && (!(next instanceof TxtTitle) || !next.mustKeepAsVirtual())) {
				this.merge(next, batch);
				return;
			}
		}
		//on va juste dplacer le caret
		super.deleteByCaret(fromInside, backward, batch);
	}

	insertDiffMix(annot: IDiffAnnotMixCt) {
		WEDLET.diffLib.initDiffOnPara(this, annot);
	}

	removeDiffMix(annot: IDiffAnnotMixCt) {
		WEDLET.diffLib.removeDiffOnPara(this, annot);
	}

}

