TxtParaLike.prototype.isUnwrappable = isUnwrappable_inline;
TxtParaLike.prototype.unwrapSeq = unwrapSeq_inline;
TxtParaLike.prototype.merge = merge_elts;
TxtParaLike.prototype.hasMetaDefs = hasMetaDefs_multi;
TxtParaLike.prototype.getMetaDefs = getMetaDefs_multi;

/** Block contenant d'autres blocks de niveau paragraphes : para, table, liste... (txt-li, txt-cell, txt-div). */
export abstract class TxtParaParent extends TxtBlock {

	get txtWedModels(): TxtModel[] {return this.model.txtWedModels || this.txtRoot.wedConfig.preferedBlockTxtModels}

	/** Recherche un noeud virtuel dans le contenu fils quelquesoit le nom de balise et le role. */
	findVirtualTxtElt(parent: Element, model: IWedletModel, before: Element): TxtElement {
		if (model.nodeType == ENodeType.element) {
			for (let ch = before ? before.previousElementSibling : parent.lastElementChild; ch; ch = ch.previousElementSibling) {
				if (IS_TxtElement(ch) && ch.isVirtual()) return ch;
			}
		} else if (model.nodeType == ENodeType.text) {
			for (let ch = before ? before.previousElementSibling : parent.lastElementChild; ch; ch = ch.previousElementSibling) {
				if (ch instanceof TxtStr && ch.isVirtual()) return ch;
			}
		}
		return null;
	}

	onChildWedletsChange() {
		adjustVirtualsPara(this);
	}

	unwrapSeqFragment(fromChild: TxtElement, toChild: TxtElement, prevChild: TxtElement, nextChild: TxtElement, batch: IDocBatch, addSelAfter: boolean): boolean {
		const docHolder = this.wedMgr.docHolder;
		const xa = this.wedAnchor;
		const newLi: IJmlSet = [];
		docHolder.getStruct(xa).createContent(newLi);
		const xmlOffsetFrom = computeOffset(fromChild);
		const xmlOffsetTo = computeOffset(toChild);
		if (prevChild && nextChild) {
			//On split le container en deux.
			//on dplace le fils suivants
			const xaNext = XA.newBd(xa).incrAtDepth(-1, 1).xa;
			batch.insertJml(xaNext, newLi);
			const content = exportFragment(this, xmlOffsetTo + 1, this, Number.MAX_SAFE_INTEGER);
			const xaTarget = XA.newBd(xaNext).append(0).freeze();
			batch.moveSequence(XA.newBd(xa).append(xmlOffsetTo + 1).xa, content, xaTarget);
			if (addSelAfter) batch.extendSelAfterSeq(xaTarget, JML.lengthJmlOrText(content));
		}
		if (!prevChild) {
			//On unwrap les premiers fils
			//  On insre un LI avant
			batch.insertJml(xa, newLi);
			//  Et on dplace la squence
			const content = exportFragment(this, xmlOffsetFrom, this, xmlOffsetTo + 1);
			const xaTarget = XA.newBd(xa).append(0).freeze();
			batch.moveSequence(XA.newBd(xa).incrAtDepth(-1, 1).append(xmlOffsetFrom).xa, content, xaTarget);
			if (addSelAfter) batch.extendSelAfterSeq(xaTarget, xmlOffsetTo + 1 - xmlOffsetFrom);
		} else {
			//On unwrap les derniers  fils
			//  On insre un LI aprs
			const xaNext = XA.newBd(xa).incrAtDepth(-1, 1).xa;
			batch.insertJml(xaNext, newLi);
			//  Et on dplace la squence
			const content = exportFragment(this, xmlOffsetFrom, this, xmlOffsetTo + 1);
			const xaTarget = XA.newBd(xaNext).append(0).freeze();
			batch.moveSequence(XA.append(xa, xmlOffsetFrom), content, xaTarget);
			if (addSelAfter) batch.extendSelAfterSeq(xaTarget, xmlOffsetTo + 1 - xmlOffsetFrom);
		}
		return true;
	}
}

export class TxtPara extends TxtParaLike {
	model: TxtParaModel;

	get isParaSibling(): boolean {return true}

	moveAsStandardPara(ctnTarget: TxtElement | TxtRoot, offsetTarget: number, batch: IDocBatch) {
		//On est le standard para, donc on se dplace intgralement.
		const paraXa = this.wedAnchor;
		batch.moveSequence(paraXa, batch.docHolder.getContent(paraXa), XA.append(ctnTarget.wedAnchor, offsetTarget));
	}

	mustKeepAsVirtual(): boolean {
		//le dernier paragraphe du container ou entre deux blocs (ou item de liste simple) doit rester en virtual.
		return !(findTxtEltPreviousSibling(this) instanceof TxtPara || findTxtEltNextSibling(this) instanceof TxtPara);
	}

	onChildEmptied(willBeEmptied: (xa: IXAddr) => boolean): IXAddr {
		//Les dernier para est toujours supprimable indpendamment du schma (car un para vide sera supprim  l'enregistrement rel du flux)
		// pour mettre en erreur le container.
		return WEDLET.onChildEmptied(this, willBeEmptied, this.wedParent === this.txtRoot.wedlet ? () => true : null);
	}
}

window.customElements.define("txt-para", TxtPara);

export class TxtUL extends TxtBlock {
	model: TxtULModel | TxtSLModel;

	get isParaSibling(): boolean {return true}

	isUnwrappable(target?: "list" | "div"): "list" | "div" | null {return target !== "div" ? "list" : findTxtEltParent(this)?.isUnwrappable(target)}

	getChildrenModelToDisplay(): TxtModel[] {return this.model.childrenModels}

	unwrapSeq(target: "list" | "div" | null, fromChild: TxtElement, toChild: TxtElement, batch: IDocBatch, addSelAfter: boolean): boolean {
		if (target === "div") return findTxtEltParent(this)?.unwrapSeq(target, this, this, batch, addSelAfter) || false;
		const prevChild = DOM.findPreviousSibling(fromChild, IS_TxtElementReal);
		const nextChild = DOM.findNextSibling(toChild, IS_TxtElementReal);

		const docHolder = this.wedMgr.docHolder;
		const parentTarget = findTxtEltOrRootParent(this);
		const offsetTarget = !prevChild ? this.xaPart : this.xaPart + 1;
		const xaTarget = XA.append(parentTarget.wedAnchor, offsetTarget);

		if (prevChild && nextChild) {
			//un split de la liste est ncessaire.
			//On dplace  partir de nextChild dans une nouvelle liste
			const newList: IJmlSet = [];
			const xa = this.wedAnchor;
			docHolder.getStruct(xa).createContent(newList);
			const xaNew = XA.newBd(xa).incrAtDepth(-1, 1).xa;
			batch.insertJml(xaNew, newList);
			const content = exportFragment(this, nextChild.xaPart, this, Number.MAX_SAFE_INTEGER);
			batch.moveSequence(XA.append(xa, nextChild.xaPart), content, XA.newBd(xaNew).append(0).xa);
		}

		let ch = fromChild;
		const unwrapBatch = docHolder.newBatch(); //batch isol pour ne pas avoir d'effets de bord du move prcdent
		while (ch) {
			if (ch instanceof TxtCaption) {
				ch.moveAsStandardPara(parentTarget, offsetTarget, unwrapBatch);
				if (addSelAfter) unwrapBatch.extendSelAfterSeq(xaTarget, 1);
				unwrapBatch.needAdjustForNextAdds();
			} else if (ch instanceof TxtLI) {
				//on suppr le LI
				const firstP = DOM.findFirstChild(ch, IS_TxtElementReal);
				if (prevChild || nextChild) unwrapBatch.deleteSequence(ch.wedAnchor, 1);
				if (firstP) {
					//on move le contenu
					const content = exportFragment(ch, firstP.xaPart, ch, Number.MAX_SAFE_INTEGER);
					unwrapBatch.insertJml(xaTarget, content);
					if (addSelAfter) unwrapBatch.extendSelAfterSeq(xaTarget, JML.lengthJmlOrText(content));
				}
				unwrapBatch.needAdjustForNextAdds();
			}
			if (ch === toChild) break;
			ch = findTxtEltNextSibling(ch);
		}
		batch.add(unwrapBatch);
		if (addSelAfter && unwrapBatch.selAfter) {
			batch.setSelAfter(unwrapBatch.selAfter.addr, unwrapBatch.selAfter.end);
			unwrapBatch.setSelAfter(null);
		}
		batch.needAdjustForNextAdds();
		if (!prevChild && !nextChild) {
			//on suppr ce container
			batch.deleteSequence(this.wedAnchor, 1);
		}
		return true;
	}

	onChildDeletedByCaret(child: TxtElement): TxtElement {
		for (let ch = findTxtEltFirstChild(this); ch; ch = findTxtEltNextSibling(ch)) {
			if (ch !== child && !ch.isVirtual()) return null; // pas vide, mme sans child.
		}
		//ce UL/OL/SL sera vide sans child;
		return this;
	}

	protected diffForeignBuildDoc(annot: IDiffAnnotForeign): Document {
		const doc = JML.jmlToDom([this.model.newJml()], DOM.newDomDoc()) as Document;
		doc.documentElement.append(doc.importNode(annot.foreignNode, true));
		return doc;
	}

	diffForeignRedrawAtEnd(this: void, wedMgr: WedMgr, commands: IDocFetch[]) {
		super.diffForeignRedrawAtEnd(wedMgr, commands);
		const root = wedMgr.wedEditor.rootNode;
		DOMSH.findFlatNext(root, root, (n: Node): n is HTMLElement => n instanceof TxtUL).classList.add("fragRoot");
	}
}

