export class TxtTable extends TxtBlock implements IWedFocusBarPointer {
	model: TxtTableModel;

	table: HTMLTableElement & ITxtWrapElt;

	get delegatedHost(): HTMLTableElement & ITxtWrapElt {return this.table}

	protected _tableLayout: TableLayout;
	get tableLayout(): TableLayout {return this._tableLayout || (this._tableLayout = new TableLayout(this))}

	constructor() {
		super();
		this.tabIndex = 0;
	}

	configWedletElt(tpl: Element, wedlet: IWedlet) {
		let style = tpl.getAttributeNS(TXT_NS, "style");
		if (style) this.setAttribute("style", style);
		const sh = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this.style.display = "flex";
		let flex = sh.appendChild(document.createElement("div"));
		flex.style.flex = "1 0 .5em";
		flex.style.cursor = "cell"; //TODO image ddie ?
		flex.addEventListener('pointerdown', onTxtTableFlexPointerDown);
		sh.appendChild(document.createElement("slot"));
		this.table = this.appendChild(document.createElement("table")) as HTMLTableElement & ITxtWrapElt;
		this.table.isTxtWrapElt = true;
		//FIXME revoir impl TxtElement.txtRoot
		Object.defineProperty(this.table, "txtRoot", {
			get(this: HTMLElement): any {return (this.parentNode as TxtTable).txtRoot}
		});
		flex = sh.appendChild(document.createElement("div"));
		flex.style.flex = "1 0 .5em";
		flex.style.cursor = "cell";
		flex.addEventListener('pointerdown', onTxtTableFlexPointerDown);
		style = tpl.getAttributeNS(TXT_NS, "tableStyle");
		if (style) this.table.setAttribute("style", style);
		this.table.style.tableLayout = "fixed";
		return this;
	}

	get isParaSibling(): boolean {return true}

	isRemovableByCaret(): boolean {return false}

	countCols(): number {
		let res = 0;
		for (let ch = this.table.firstElementChild; ch; ch = ch.nextElementSibling) {
			if (ch instanceof TxtCol) res++;
			else if (ch instanceof TxtRow) break;
		}
		return res;
	}

	getCol(logicOffs: number): TxtCol {
		let col = DOM.findFirstChild(this.table, TXTTABLE.IS_TxtCol);
		for (let i = 0; i < logicOffs; i++) if (col) col = DOM.findNextSibling(col, TXTTABLE.IS_TxtCol);
		return col;
	}

	onChildWedletsChange() {
		let nextElt = findTxtEltFirstChild(this.table);
		if (this.model.captionModel) {
			if (!(nextElt instanceof TxtCaption)) {
				createChildToDisplay(this, this.model.captionModel, nextElt);
			}
		}
		while (nextElt instanceof TxtCaption) nextElt = findTxtEltNextSibling(nextElt);
		if (!(nextElt instanceof TxtCol)) {
			createChildToDisplay(this, this.model.colModels[0], nextElt);
		}
		while (nextElt instanceof TxtCol) nextElt = findTxtEltNextSibling(nextElt);
		if (!(nextElt instanceof TxtRow)) {
			createChildToDisplay(this, this.model.rowModels[0], nextElt);
		}
	}

	get focusBar(): BarActions<ITxtTableContext> {
		if (!this.tableLayout.enabled) return null;
		const txtRoot = this.txtRoot;
		const tableBars = txtRoot.txtRootProps.tableBars;
		if (!tableBars) return null;
		const bar = tableBars[this.txtRole];
		if (bar) {
			bar.actionContext.focusedElt = txtRoot;
			bar.actionContext.txTable = this;
			bar.actionContext.logicTable = this.tableLayout.logicTable;
		}
		return bar;
	}

	focus() {
		this.tableLayout.drawTableLayout();
	}

	diffTable?: WedDiffTable;

	insertDiffForeign(annot: ISkDiffAnnot) {
		if (!this.diffTable) this.diffTable = (document.createElement("wed-diff-table") as WedDiffTable).initTable(this);
		this.diffTable.addAnnot(annot);
	}

	removeDiffForeign(annot: ISkDiffAnnot) {
		if (this.diffTable) this.diffTable.removeAnnot(annot);
	}
}

window.customElements.define("txt-table", TxtTable);


