export class TxtObject extends TxtBlock implements IWedFocusBarPointer, IContextMenuActionsPointer<IWedMgrPointer> {

	model: TxtObjectModel;

	constructor() {
		super();
		this.tabIndex = 0;
		this.addEventListener("pointerdown", onPointerDownObject);
	}

	isFocusableByCaret(): boolean {return true}

	isRemovableByCaret(): boolean {return true}

	get isParaSibling(): boolean {return true}

	isEmpty(): boolean {return false}

	findOrCreateTxtStr(last: boolean): TxtStr {return null}

	findOrCreateTxtStrOrObject(last: boolean): TxtStr | TxtObject | InlObject {return this}

	get focusBar(): BarActions<IWedletActionCtx> {
		for (let ch = this.firstElementChild; ch; ch = ch.nextElementSibling) {
			const fb = (ch as IWedFocusBarPointer).focusBar;
			if (fb) return fb;
		}
		return this.txtRoot.focusBar;
	}

	get ctxMenuActions(): IContextMenuActions<IWedMgrPointer> {
		for (let ch = this.firstElementChild; ch; ch = ch.nextElementSibling) {
			const mn = (ch as IContextMenuActionsPointer<IWedMgrPointer>).ctxMenuActions;
			if (mn) return mn;
		}
		return this.txtRoot.ctxMenuActions;
	}

	insertDiffInMeta(annot: ISkDiffAnnot) {
		if (!this.model.addDiffCustom(this, annot)) super.insertDiffInMeta(annot);
	}

	removeDiffInMeta(annot: ISkDiffAnnot) {
		if (!this.model.removeDiffCustom(this, annot)) super.removeDiffInMeta(annot);
	}
}

