export class TxtCol extends TxtElement {

	model: TxtColModel;

	get widthMgr(): ITxtColWidthMgr {return this.model.widthMgr(this.wedMgr)}

	constructor() {
		super();
		this.style.display = "table-column";
	}

	insertElement(parent: Element, insertBefore?: Node, slotName?: string, caller?: IWedChildrenElt) {
		if (parent instanceof TxtTable) parent = parent.table;
		if (caller) (this as IElementWedlet).fromChildrenElt = caller;
		parent.insertBefore(this, insertBefore);
	}

	getLogicOffset(): number {
		let offs = 0;
		let prev: TxtCol = this;
		while ((prev = DOM.findPreviousSibling(prev, TXTTABLE.IS_TxtCol))) offs++;
		return offs;
	}

	findOrCreateTxtStr(last: boolean): TxtStr {return null}

	findOrCreateTxtStrOrObject(last: boolean): TxtStr | TxtObject | InlObject {
		const table = findTxtEltParent(this) as TxtTable;
		const logicTable = table.tableLayout.logicTable;
		const txtCell = logicTable.getTxtCellAt(last ? logicTable.logicRows.length - 1 : 0, this.getLogicOffset());
		return txtCell.findOrCreateTxtStrOrObject(last);
	}

	insertDiffMark(annot: IDiffAnnotMark) {
		(this.wedParent as TxtTable).insertDiffForeign(annot);
	}

	removeDiffMark(annot: IDiffAnnotMark) {
		(this.wedParent as TxtTable).removeDiffForeign(annot);
	}

	insertDiffForeign(annot: IDiffAnnotForeign) {
		(this.wedParent as TxtTable).insertDiffForeign(annot);
	}

	removeDiffForeign(annot: IDiffAnnotForeign) {
		(this.wedParent as TxtTable).removeDiffForeign(annot);
	}
}

window.customElements.define("txt-col", TxtCol);

export class TxtRow extends TxtBlock {
	model: TxtRowModel;

	constructor() {
		super();
		this.style.display = "table-row";
	}

	insertElement(parent: Element, insertBefore?: Node, slotName?: string, caller?: IWedChildrenElt) {
		if (parent instanceof TxtTable) parent = parent.table;
		if (caller) (this as IElementWedlet).fromChildrenElt = caller;
		parent.insertBefore(this, insertBefore);
	}

	onChildWedletsChange() {
		//FIXME algo table avec rowspan / colspan pour dfinir le nb de cell virtuelles  injecter. Algo ici ou dans SkMeta via autoComplete ?
		if (!(this.previousElementSibling instanceof TxtRow) && !DOM.findNext(this, this, IS_TxtStr)) {
			//Impl. temporaire:
			//1re row de la table on garantit  minima de la prsence d'une cell.
			//Pour respecter l'obligation d'avoir  minima un TxtStr dans la table
			//sinon pb avec TxtTable.findOrCreateTxtStrOrObject() par ex qui impose de retourner quelque chose.
			const cellModel = (this.model as TxtRowModel).cellModels[0];
			if (cellModel) createChildToDisplay(this, cellModel);
		}
	}

	/** Compte de nb de cells de cette row, sans compter les occupations issues des colspan des rows suprieures */
	get countIntrinsicCols(): number {
		let res = 0;
		for (let ch = this.firstElementChild; ch; ch = ch.nextElementSibling) {
			if (ch instanceof TxtCell) res += (parseInt(ch.getAttribute('colspan'), 10) || 1);
		}
		return res;
	}

	insertDiffMark(annot: IDiffAnnotMark) {
		const lastCell = this.lastElementChild as TxtElement & IWedAnnotPointer;
		if (lastCell) {
			const mark = (document.createElement("wed-diff-mark") as WedDiffMark).initDiffAnnot(this, annot, this.wedlet);
			DOM.setStyle(lastCell, "position", "relative");
			lastCell.appendChild(mark);
		}
	}

	removeDiffMark(annot: IDiffAnnotMark) {
		this.removeAttribute("annot-diff");
		const lastCell = this.lastElementChild as TxtElement & IWedAnnotPointer;
		if (lastCell) lastCell.removeDiffMark(annot);
	}

	insertDiffForeign(annot: IDiffAnnotForeign) {
		const table = this.wedParent as TxtTable;
		table.insertDiffForeign(annot);
	}

	removeDiffForeign(annot: IDiffAnnotForeign) {
		const table = this.wedParent as TxtTable;
		table.removeDiffForeign(annot);
	}
}

