export class TxtCell extends HTMLTableCellElement {
	model: TxtCellModel;

	constructor() {
		super();
		this.style.wordBreak = "break-word"; //pour viter qu'un mot trop long du caption perturbe la largeur des colonnes.
	}

	get spanMgr(): ITxtCellSpanMgr {
		const lib = this.model.spanMgrLib ? this.wedMgr.wedModel.jsLibs[this.model.spanMgrLib] : null;
		return lib ? lib[this.model.spanMgrKey] || cellSpanMgrDefault : cellSpanMgrDefault;
	}

	getRowSpan(): number {
		const sp = this.getAttribute("rowspan");
		return sp ? Math.max(1, parseInt(sp, 10)) : 1;
	}

	getColSpan(): number {
		const sp = this.getAttribute("colspan");
		return sp ? Math.max(1, parseInt(sp, 10)) : 1;
	}

	/** une cell n'est jamais juge vide, et ne peut tre supprime. */
	isEmpty(): boolean {return false}

	isRemovableByCaret(): boolean {return false}

	isUnwrappable(): boolean {return false}

	insertDiffMark(annot: IDiffAnnotMark) {
		//fusion de cell ou insert de colonne, pas reprsent.
		(this.wedParent.wedParent as TxtTable).insertDiffForeign(annot);
		this.setAttribute("annot-diff", "diffadd");
	}

	removeDiffMark(annot: IDiffAnnotMark) {
		(this.wedParent.wedParent as TxtTable).removeDiffForeign(annot);
		this.removeAttribute("annot-diff");
	}
}

