export class TxtLI extends TxtParaParent {
	model: TxtLIModel;

	mustKeepAsVirtual(): boolean {
		//le dernier item de liste doit rester en virtual.
		if (findTxtEltPreviousSibling(this) instanceof TxtLI) return false;
		if (findTxtEltNextSibling(this) instanceof TxtLI) return false;
		return true;
	}

	isMergeable(next: TxtElement): boolean {return next instanceof TxtLI}

	isUnwrappable(target?: "list" | "div"): "list" | "div" | null {return target !== "div" ? "list" : findTxtEltParent(this)?.isUnwrappable(target)}

	unwrapSeq(target: "list" | "div" | null, fromChild: TxtElement, toChild: TxtElement, batch: IDocBatch, addSelAfter: boolean): boolean {
		if (target === "div") return findTxtEltParent(this)?.unwrapSeq(target, this, this, batch, addSelAfter) || false;
		const prevChild = DOM.findPreviousSibling(fromChild, IS_TxtElementReal);
		const nextChild = DOM.findNextSibling(toChild, IS_TxtElementReal);
		if (!prevChild && !nextChild) {
			//On veut unwrapper l'intgralit des fils, on remove ce wrapper.
			//Un LI ne se unwrap pas, on remonte  la liste parent
			return findTxtEltParent(this).unwrapSeq(target, this, this, batch, addSelAfter);
		}
		return this.unwrapSeqFragment(fromChild, toChild, prevChild, nextChild, batch, addSelAfter);
	}

	onChildDeletedByCaret(child: TxtElement): TxtElement {
		for (let ch = findTxtEltFirstChild(this); ch; ch = findTxtEltNextSibling(ch)) {
			if (ch !== child && !ch.isVirtual()) return null; // pas vide, mme sans child.
		}
		//LI sera vide sans child;
		const parent = findTxtEltParent(this);
		return parent ? parent.onChildDeletedByCaret(this) || this : this;
	}
}

TxtLI.prototype.merge = merge_elts;
TxtLI.prototype.hasMetaDefs = hasMetaDefs_multi;
TxtLI.prototype.getMetaDefs = getMetaDefs_multi;
window.customElements.define("txt-li", TxtLI);

export class TxtDiv extends TxtParaParent {

	isUnwrappable(target?: "list" | "div"): "list" | "div" | null {return target !== "list" ? "div" : findTxtEltParent(this)?.isUnwrappable(target)}

	get isParaSibling(): boolean {return true}

	unwrapSeq(target: "list" | "div" | null, fromChild: TxtElement, toChild: TxtElement, batch: IDocBatch, addSelAfter: boolean): boolean {
		if (target === "list") return findTxtEltParent(this)?.unwrapSeq(target, this, this, batch, addSelAfter) || false;
		//On supprime ce div
		const xa = this.wedAnchor;
		const content = exportFragment(this, fromChild.xaPart, this, Number.MAX_SAFE_INTEGER);
		batch.deleteSequence(xa, 1);
		batch.insertJml(xa, content);
		if (addSelAfter) batch.extendSelAfterSeq(xa, JML.lengthJmlOrText(content));
		return true;
	}
}

TxtDiv.prototype.hasMetaDefs = hasMetaDefs_multi;
TxtDiv.prototype.getMetaDefs = getMetaDefs_multi;
