export abstract class InlElement extends TxtElement {

	/**
	 * Lors de slections coises avec une balises existante, celle qui sera splitte est la balise inline
	 * qui a la plus faible priorit.
	 */
	get splitPriority(): number {return Infinity}

	findOrCreateCaretableSibling(before: boolean): TxtStr {
		const next = before ? findTxtEltPreviousSibling(this) : findTxtEltNextSibling(this);
		if (next instanceof TxtStr) return next;
		const parent = findTxtEltParent(this);
		console.trace("CreateOnTheFly: txtStr");
		const strModel = this.model.strModel || this.txtRoot.wedConfig.strModel;
		const wedlet = strModel.createWedlet(parent);
		wedlet.configWedletElt(strModel.config, wedlet);
		parent.insertBefore(wedlet, before ? this : this.nextSibling) as TxtStr;
		return wedlet.bindAsVirtual() as TxtStr;
	}

	insertDiffMark(annot: IDiffAnnotMark) {
		if (annot.wrap) {
			(document.createElement("wed-diff-wrap-mark") as WedDiffWrapMark).initDiffWrapAnnot(this, annot, this.wedlet);
		} else {
			//Pas d'action dans l'inline pour le moment.
			(document.createElement("wed-diff-mark") as WedDiffMark).initDiffAnnot(this, annot, this.wedlet, null, true).defaultInject();
		}
	}
}

InlElement.prototype.isUnwrappable = isUnwrappable_inline;
InlElement.prototype.unwrapSeq = unwrapSeq_inline;
