/**
 * Bind avec un noeud texte.
 * TODO Pourra contenir plusieurs noeuds texte HTML et des marks (annotations...).
 */
export class TxtStr extends TxtElement implements ICharsWedlet, IFixCaret, IFixSelection, ISkAnnotFocuser {

	model: TxtStrModel;

	protected _xmlText: string;

	refreshBindValue(val: IJmlNode) {
		if (this.isVirtual()) this.classList.add("virtual");
		else this.classList.remove("virtual");
		this._xmlText = JML.jmlNode2value(val) || "";
		if (this._xmlText) insertXmlText(this, resetXmlText(this), 0, this._xmlText);
		else resetXmlText(this);
		if ("forceSpellCheck" in this) (this as any).forceSpellCheck(); //HTML 5.2 https://html.spec.whatwg.org/multipage/interaction.html#dom-forcespellcheck
	}


	insertChars(from: number, chars: string, msg: IXmlMsg) {
		this._xmlText = LANG.stringInsert(this._xmlText, from, chars);
		if (!this.txtRoot.webUpdateDone) insertXmlText(this, this, from, chars);
	}

	deleteChars(from: number, len: number, msg: IXmlMsg) {
		this._xmlText = LANG.stringDelete(this._xmlText, from, len);
		if (!this.txtRoot.webUpdateDone) deleteXmlText(this, this, {start: from, end: from + len});
	}

	replaceChars(chars: string, msg: IXmlMsg) {
		const oldLen = this.getXmlTextLength();
		this._xmlText = chars;
		if (!this.txtRoot.webUpdateDone) {
			deleteXmlText(this, this, {start: 0, end: oldLen});
			insertXmlText(this, this, 0, chars);
		}
	}

	bindWithAttr(nameAttr: string, value: string): void | Promise<void> {/* not used */}

	/** le point de focus est dans ce TxtStr, on le dplace. */
	moveCaretFrom(selMgr: TxtSelMgr, backward: boolean, alter: 'extend' | 'move' = 'move', granularity: 'word' | 'character' = 'character') {
		let focusNode = selMgr.focusNode as ITxtStrTextNode;
		let offset = selMgr.focusOffset;
		if (backward) {
			for (; ;) {
				if (offset > (focusNode.startBoundary ? 1 : 0)) {
					//Si on est au milieu d'un noeud texte, on laisse l'implmentation native grer.
					selMgr.modify(alter, backward ? 'backward' : 'forward', granularity);
					return;
				}
				let found = false;
				focusNode = DOM.findPrevious(focusNode, this, (n): n is ITxtStrTextNode => {
					if (granularity === 'character' && n.localName === 'x-nbsp') found = true;
					return n.nodeType === ENodeType.text;
				});
				if (!focusNode) break;
				offset = focusNode.nodeValue.length - (focusNode.endBoundary ? 1 : 0);
				if (found) break;
			}
		} else {
			for (; ;) {
				if (offset < focusNode.nodeValue.length - (focusNode.endBoundary ? 1 : 0)) {
					//Si on est au milieu d'un noeud texte, on laisse l'implmentation native grer.
					selMgr.modify(alter, backward ? 'backward' : 'forward', granularity);
					return;
				}
				let found = false;
				focusNode = DOM.findNext(focusNode, this, (n): n is ITxtStrTextNode => {
					if (granularity === 'character' && n.localName === 'x-nbsp') found = true;
					return n.nodeType === ENodeType.text;
				});
				if (!focusNode) break;
				offset = focusNode.startBoundary ? 1 : 0;
				if (found) break;
			}
		}
		if (focusNode) {
			if (alter === 'extend') selMgr.setSelFocus(focusNode, offset);
			else selMgr.collapse(focusNode, offset);
		} else {
			//on sort
			this.moveCaretOut(selMgr, backward, alter);
		}
	}

	mustKeepAsVirtual(): boolean {
		//si on est le dernier TxtStr entre deux blocs ou en bordure du para.
		return !(findTxtEltPreviousSibling(this) instanceof TxtStr || findTxtEltNextSibling(this) instanceof TxtStr);
	}

	setCaretIn(selMgr: TxtSelMgr, fromStart: boolean, alter: 'extend' | 'move' = 'move') {
		if (this.isEmpty()) {
			//on recherche un str frre non vide de prfrence.
			const sibling = fromStart ? findTxtEltNextSibling(this) : findTxtEltPreviousSibling(this);
			if (sibling instanceof TxtStr) {
				sibling.setCaretIn(selMgr, fromStart, alter);
				return;
			}
		}
		const node = fromStart ? DOM.findNext(this, this, IS_TxtStrTextNode) : DOM.findPreviousIn(this, IS_TxtStrTextNode);
		const offset = fromStart ? node.startBoundary ? 1 : 0 : node.data.length - (node.endBoundary ? 1 : 0);
		if (alter === 'move') {
			selMgr.collapse(node, offset);
		} else {
			selMgr.setSelFocus(node, offset);
		}
	}

	moveCaretOut(selMgr: TxtSelMgr, throwStart: boolean, alter: 'extend' | 'move' = 'move') {
		let next = throwStart ? findTxtEltPreviousSibling(this) : findTxtEltNextSibling(this);
		while (next && next instanceof TxtStr) {
			//cas particulier des noeuds textes adjacents vides.
			if (next.isEmpty()) {
				//vide, on saute
				next = throwStart ? findTxtEltPreviousSibling(next) : findTxtEltNextSibling(next);
			} else {
				next.setCaretIn(selMgr, !throwStart, alter);
				next.moveCaretFrom(selMgr, throwStart, alter);
				return;
			}
		}
		if (next) {
			next.setCaretIn(selMgr, !throwStart, alter);
		} else {
			const p = findTxtEltParent(this);
			if (p) p.moveCaretOut(selMgr, throwStart, alter);
			else if (alter === 'move') {
				//pas de p (TxtRootStr), on place le curseur  la limite de ce noeud
				this.setCaretIn(selMgr, throwStart);
			}
		}
	}

	fixupCaret(selMgr: TxtSelMgr, oldFocus: Node, oldOffset: number): boolean {
		//console.log("before fixupCaret:", sel.focusNode, sel.focusOffset);
		const focusNode = selMgr.focusNode;
		if (IS_TxtStrTextNode(focusNode)) {
			if (selMgr.focusOffset === 0 && focusNode.startBoundary) {
				//on est avant le BOUND_CHAR: position interdite
				if (focusNode === oldFocus && oldOffset === 1) {
					//la sel indique qu'on vient de la droite, on va chercher un texte prcdent
					const prevTxt = DOM.findPrevious(focusNode, this, IS_TxtStrTextNode);
					if (prevTxt) {
						selMgr.collapse(prevTxt, prevTxt.data.length - (prevTxt.endBoundary ? 1 : 0));
						return true;
					} else {
						const prev = findTxtStrPrevious(this);
						if (prev) {
							prev.setCaretIn(selMgr, false);
							return true;
						}
					}
				}
				selMgr.collapse(focusNode, 1);
				return true;
			} else if (selMgr.focusOffset === focusNode.nodeValue.length && focusNode.endBoundary) {
				//on est aprs le BOUND_CHAR: position interdite
				if (focusNode === oldFocus && oldOffset === selMgr.focusOffset - 1) {
					//la sel indique qu'on vient de la gauche, on va chercher un texte suivant
					const nextTxt = DOM.findNext(focusNode, this, IS_TxtStrTextNode);
					if (nextTxt) {
						selMgr.collapse(nextTxt, nextTxt.startBoundary ? 1 : 0);
						return true;
					} else {
						const next = findTxtStrNext(this);
						if (next) {
							next.setCaretIn(selMgr, true);
							return true;
						}
					}
				}
				selMgr.collapse(focusNode, focusNode.nodeValue.length - 1);
				return true;
			}
		} else {
			console.log("fixupCaret:: Caret hors textNode in txt-str!!!");
		}
		return false;
	}

	fixupSelection(selMgr: TxtSelMgr, what: number, oldFocus: Node, oldOffset: number): boolean {
		//console.log("fixupSelection", what, sel.focusNode, sel.focusOffset, DOMSH.findDocumentOrShadowRoot(this).activeElement);
		let updated = false;
		if (what & 1) {
			const anchorNode = selMgr.anchorNode;
			if (IS_TxtStrTextNode(anchorNode)) {
				const anchorOffset = selMgr.anchorOffset;
				if (anchorOffset === 0) {
					selMgr.setSelAnchor(anchorNode, anchorNode.startBoundary ? 1 : 0);
					updated = true;
				} else if (anchorOffset === anchorNode.length && anchorNode.endBoundary) {
					selMgr.setSelAnchor(anchorNode, anchorOffset - 1);
					updated = true;
				}
			} else {
				console.log("todo: Selection AnchorNode hors textNode!!!", selMgr.anchorNode);
			}
		}
		if (what & 2) {
			const focusNode = selMgr.focusNode;
			const focusOffset = selMgr.focusOffset;
			if (IS_TxtStrTextNode(focusNode)) {
				if (focusOffset === 0 && focusNode.startBoundary) {
					//on est avant le BOUND_CHAR: position interdite
					if (focusNode === oldFocus && oldOffset > 0) {
						//la sel indique qu'on vient de la droite, on va chercher un texte prcdent
						const prevTxt = DOM.findPrevious(focusNode, this, IS_TxtStrTextNode);
						if (prevTxt) {
							selMgr.setSelFocus(prevTxt, prevTxt.data.length - (prevTxt.endBoundary ? 1 : 0));
							return true;
						} else {
							const prev = findTxtStrPrevious(this);
							if (prev) {
								prev.setCaretIn(selMgr, false, 'extend');
								return true;
							}
						}
					}
					selMgr.setSelFocus(focusNode, focusNode.startBoundary ? 1 : 0);
					return true;
				} else if (focusOffset === focusNode.nodeValue.length && focusNode.endBoundary) {
					//on est aprs le BOUND_CHAR: position interdite
					if (focusNode === oldFocus && oldOffset < focusOffset) {
						//la sel indique qu'on vient de la gauche, on va chercher un texte suivant
						const nextTxt = DOM.findNext(focusNode, this, IS_TxtStrTextNode);
						if (nextTxt) {
							selMgr.setSelFocus(nextTxt, nextTxt.startBoundary ? 1 : 0);
							return true;
						} else {
							const next = findTxtStrNext(this);
							if (next) {
								next.setCaretIn(selMgr, true, 'extend');
								return true;
							}
						}
					}
					selMgr.setSelFocus(focusNode, focusNode.nodeValue.length - (focusNode.endBoundary ? 1 : 0));
					return true;
				}
			} else {
				console.log("todo : Selection FocusNode hors textNode!!!", focusNode);
			}
		}
		return updated;
	}

	findOrCreateTxtStr(last: boolean): TxtStr {return this}

	findOrCreateTxtStrOrObject(last: boolean): TxtStr | TxtObject | InlObject {return this}

	getXmlText(): string {return this._xmlText}

	getXmlTextLength() {return this._xmlText.length}

	/**
	 * @param from noeud texte ou noeud fils du TxtStr (annot, mark...)
	 */
	getXmlOffset(from: Node, webOffset: number): number {
		return getXmlOffset(this, from, webOffset);
	}

	getWebOffset(xmlOffset: number): [ITxtStrTextNode, number] {
		return getWebOffset(this, xmlOffset);
	}

	/** Ce point dans les noeuds HTML correspond-il  l'offset 0 du noeud texte xml. */
	isXmlStartOffset(from: Node, webOffset: number): boolean {
		return DOM.findNext(this, this, IS_TxtStrTextNode) === from && (webOffset <= 1 || xmlLen(from as ITxtStrTextNode) === 0);
	}

	/** Ce point dans les noeuds HTML correspond-il  la longueur du noeud texte xml. */
	isXmlEndOffset(from: Node, webOffset: number): boolean {
		return DOM.findPreviousIn(this, IS_TxtStrTextNode) === from && (webOffset >= from.nodeValue.length - 1 || xmlLen(from as ITxtStrTextNode) === 0);
	}

	/**
	 * Modification des donnes saisies ou colles avant injection dans le batch.
	 * Rellement exploitable uniquement dans le cadre d'un TxtStrAlone.
	 */
	transformInsertData(data: string): string {return data}

	/**
	 * Remonte les balises inlines imbriques par le dbut ou la fin  condition qu'aucun autre contenu soit travers.
	 * Retourne le plus haut InlWrap possible ou null si aucun InlWrap ne correspond.
	 */
	getTopInlineParent(from: 'start' | 'end'): InlWrap | null {
		let topInline: InlWrap = null;
		let child: TxtElement = this;
		let parent = findTxtEltParent(child);
		if (from === 'start') {
			while (parent instanceof InlWrap) {
				if (findTxtEltFirstChild(parent) === child) {
					//on est le 1er fils on remonte,
					topInline = parent;
					child = parent;
					parent = findTxtEltParent(parent);
				} else {
					//on n'est pas le 1er fils, on sort
					return topInline;
				}
			}
		} else {
			while (parent instanceof InlWrap) {
				if (findTxtEltLastChild(parent) === child) {
					//on est le dernier fils on remonte,
					topInline = parent;
					child = parent;
					parent = findTxtEltParent(parent);
				} else {
					//on n'est pas le dernier fils, on sort
					return topInline;
				}
			}
		}
		return topInline;
	}

	/** Retourne true si ce texte est le 1er contenu visible du para, mme s'il est inclus dans des balises inlines. */
	isFirstContentInPara(): boolean {
		const para = findTxtParaLikeFrom(this);
		return DOM.findPrevious(this, para, (n: Node): n is TxtElement => {
			if (n instanceof InlWrap) return false; //on saute les balises inlines
			if (IS_TxtElementReal(n)) return true; //bind non virtual trouv, donc on n'est pas le premeier du para.
			return false;
		}) == null;
	}

	deleteByCaret(fromInside: boolean, backward: boolean, batch: IDocBatch) {
		if (fromInside || this.isRemovableByCaret()) {
			const sibling = backward ? findTxtEltPreviousSibling(this) : findTxtEltNextSibling(this);
			if (sibling) {
				if (this.shouldBeCleaned()) this.remove();
				sibling.deleteByCaret(false, backward, batch);
			} else {
				const parent = findTxtEltParent(this);
				if (this.shouldBeCleaned()) this.remove();
				if (parent) parent.deleteByCaret(true, backward, batch);
			}
		} else {
			const selMgr = this.txtRoot.selMgr;
			this.setCaretIn(selMgr, !backward);
			selMgr.modify('extend', backward ? 'backward' : 'forward', 'character');
			deleteWebRange(this.txtRoot, selMgr.range, batch, backward, true);
			selMgr.collapseToStart();
		}
	}

	isEmpty(): boolean {
		return this._xmlText.length === 0;
	}

	isRemovableByCaret(): boolean {
		return this.isEmpty();
	}

	shouldBeCleaned() {
		if (!this.isVirtual()) return false;
		return findTxtEltPreviousSibling(this) instanceof TxtStr || findTxtEltNextSibling(this) instanceof TxtStr;
	}

	/** requestIdle en cours. */
	redrawAnnotRequest?: number;
	redrawAnnotExecutor?: (deadline: RequestIdleDeadline) => void;

	onAddedSkAnnot(annot: ISkAnnot, xaTarget: IXAddr): void {
		if (annot instanceof SkSearchTextAnnot || (WEDLET.diffLib?.isDiffAnnot(annot) && annot.diffSession === this.wedMgr.docHolder.getDiffSession())) {
			planRedrawTxtStrAnnots(this);
		} else {
			super.onAddedSkAnnot(annot, xaTarget);
		}
	}

	onRemovedSkAnnot(annot: ISkAnnot, xaTarget: IXAddr): void {
		if (annot instanceof SkSearchTextAnnot || WEDLET.diffLib?.isDiffAnnot(annot)) {
			//console.log(":::onRemovedSkAnnot",annot);
			planRedrawTxtStrAnnots(this);
			//removeAnnotInTxtStr(this, annot);
		} else {
			super.onRemovedSkAnnot(annot, xaTarget);
		}
	}

	focusSkAnnot(annot: ISkAnnot): boolean {
		//console.log(":::focusSkAnnot",annot);
		if (isSkTextAnnot(annot)) {
			const [anc, ancOffs] = this.getWebOffset(annot.offsetStart);
			const [foc, focOffs] = this.getWebOffset(annot.offsetStart + annot.len);
			const txtRoot = this.txtRoot;
			WEDLET.ensureContainersUncollapsed(txtRoot);
			txtRoot.selMgrAsIs.setSelBounds(anc, ancOffs, foc, focOffs);
			txtRoot.selMgrAsIs.ensureSelVisible();
			return true;
		}
		return false;
	}

}

TxtStr.prototype.isUnwrappable = isUnwrappable_inline;
TxtStr.prototype.unwrapSeq = unwrapSeq_inline;
(TxtStr.prototype as any as IInsMgrWedlet).markInserts = undefined; //Pas de marques d'insertion dans le noeud texte!

window.customElements.define("txt-str", TxtStr);